.app__price_order-mainSubstrate{
    display: flex;
    position: relative;

    width: 75%;
    height: 93.3%;
    margin: auto;


    background: var(--color-ordermeet);
    box-shadow: var(--box-shadow);

    animation: OpenProblem 0.4s both;
}

@keyframes OpenProblem {
    0%{
        opacity: 0;
    }
    100%{
        opacity: 1;
    }
}

.order-mainSubstrate-content{
    display: flex;
    width: 100%;
    justify-content: right;
}

.order-content-form-arrowSeven{
    position: absolute;
    display: flex;
    position: absolute;
    top: 0;  /* position the top  edge of the element at the middle of the parent */
    left: 47%; /* position the left edge of the element at the middle of the parent */

    transform: translate(-50%, 0); /* This is a shorthand of
                                         translateX(-50%) and translateY(-50%) */
}

.order-content-form-arrowSeven img{
    width: 36.094vw;
    height: 15.208vw;
}

.order-content-form{
    display: flex;
    position: relative;
    justify-content: center;
    align-items: right;

    margin-top: 6.250vw;
    right: -1.458vw;

    flex-direction: column;
}

.order-content-form-fields{
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    margin-top: 6.6vw;
    margin-bottom: 3.438vw;
}

.order-form-field{
    display: flex;
    margin-bottom: 1.823vw;
}
.order-form-fieldL{
    display: flex;
}

.order-form-form{
    display: flex;
    align-items: center;
    justify-content: flex-end;
}

.order-form-form input[type="text"]{
    width: 23.438vw;
    border-style: none;
    padding-bottom: 0.521vw;
    padding-right: 0.208vw;
    border-bottom: 1px solid var(--color-black);


    font-family: var(--font-main);
    font-style: normal;
    font-weight: 400;
    font-size: 1.042vw;    
    /* identical to box height */

    background: transparent;
    text-align: right;

    color: var(--color-black);
}

.order-form-form input:focus{
    outline: none;
}

.order-form-form input:focus ~ .order-field-lable,
.order-form-form input:valid ~ .order-field-lable{
    transform: translateY(-1.823vw);
    font-size: 0.625vw;

    color: var(--color-fieldlable);
}

.order-field-lable{
    font-family: var(--font-main);
    font-style: normal;
    font-weight: 400;
    font-size: 1.042vw;

    text-align: right;

    color: var(--color-black);
    transition: all 0.3s ease;
}

.order-field-lable{
    position: absolute;
    overflow: hidden;
    padding-bottom: 0.521vw;
    padding-right: 0.208vw;
    pointer-events: none;
}

.order-field-lable p {
    opacity: 0;
}

.order-field-lable-error{
    position: absolute;
    overflow: hidden;
    pointer-events: none;

    padding-bottom: 0.521vw;
    padding-right: 0.208vw;
}

.order-field-lable-error p{
    font-family: var(--font-main);
    font-style: normal;
    font-weight: 400;
    font-size: 1.042vw;
    /* identical to box height */

    text-align: right;
    pointer-events: none;
    opacity: 0;
    color: var(--color-errorlablefortypereview);

}

.order-content-buttonandbutton{
    display: flex;
    align-items: center;
    justify-content: right;
}

.order-commitbutton{
    font-family: var(--font-main);
    font-style: normal;
    font-weight: 400;
    font-size: 1.042vw;

    padding: 0.885vw 0.625vw 0.885vw 5.469vw;
    color: var(--color-white);
    box-shadow: var(--box-shadow);
    background: var(--color-black);

    border-style: none;
    cursor: pointer;
}

.order-content-image{
    display: flex;
    width: 50%;
    height: 100%;
}
.order-content-image img{
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.order-content-phoneimage{
    display: none;
}

@media screen and (max-width: 1300px) {
    .app__price_order-mainSubstrate{
        height: 50%;
    }
}

@media screen and (max-width: 480px){

/*
    display: flex;
    position: absolute;

    width: 100%;
    height: 63%;

    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);

    background: rgba(250, 250, 250, 0.73);
    box-shadow: 0px 16px 40px rgba(112, 144, 176, 0.2);
*/

    .order-content-phoneimage{
        display: flex;
        position: absolute;

        width: 120%;
        height: 120vh;

        top: -5vw;
        left: -5vw;
        filter: blur(17px);

        object-fit: cover;
    }

    .app__price_order-mainSubstrate{
        position: static;

        width: 100%;
        height: auto;

        background: transparent;
        box-shadow: none;

        background: rgba(250, 250, 250, 0.78);
        box-shadow: 0px 16px 40px rgba(112, 144, 176, 0.2);
    }

    .order-mainSubstrate-content{
        padding-top: 83px;
        padding-bottom: 83px;
    }

    .order-content-image{
        display: none;
    }

    .order-content-form-arrowSeven{
        display: none;
    }

    .order-content-form{
        margin: auto;
        position: static;
    }

    .order-content-form-fields{
        margin-bottom: 55px;
        margin-top: 0px;
    }

    .order-form-field{
        margin-bottom: 35px;
    }

    .number {
        font-size: 30px!important;
    }

    .numberone{
        padding-right: 0px!important;
        padding-bottom: 13px!important;
        margin-right: 11px;
    }
    
    .order-form-form input[type="text"]{
        width: 274px;

        font-size: 15px;

        padding-bottom: 10px;
        padding-right: 0px;
    }
    
    .order-field-lable{
        font-size: 15px;

        padding-bottom: 10px;
        padding-right: 3px;
    }
    
    .order-field-lable-error{
        padding-bottom: 10px;
        padding-right: 3px;
    }

    .order-field-lable-error p{
        font-size: 15px;
    }

    .order-commitbutton{
        padding: 11px 9px 11px 57px;

        font-size: 14px;
    }

    .order-content-form-phoneexplain{
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;

        width: 100%;

        margin-bottom: 72px;
    }

    .phoneexplain-main{
        font-family: 'Prata';
        font-style: normal;
        font-weight: 400;
        font-size: 19px;
        line-height: 26px;
        /* identical to box height */


        color: #343434;
        margin-bottom: 3px;
    }

    .phoneexplain-additional{
        font-family: 'Prata';
        font-style: normal;
        font-weight: 400;
        font-size: 12px;
        line-height: 16px;

        color: rgba(52, 52, 52, 0.4);
    }

    .order-form-form input:focus ~ .order-field-lable,
    .order-form-form input:valid ~ .order-field-lable{
        transform: translateY(-22px);
        font-size: 8px;
    
        color: var(--color-fieldlable);
    }
    
}